import React from 'react';
import styled from 'styled-components';

function Title({ title, span }) {
    return (
        <TitleStyled>
            <h2>{title} <span>{span}</span></h2>
            
        </TitleStyled>
    )
}

const TitleStyled = styled.div`
    position: relative;
    h2{
        color: var(--white-color);
        font-size: 3.1rem;
        font-weight: 400;
        text-transform: uppercase;
        position: relative;
        padding-bottom: .7rem;

        @media screen and (max-width: 620px){
                font-size: 2.5rem;
        }
        @media screen and (max-width: 496px){
                font-size: 1.8rem;
            }
        
        @media screen and (max-width: 370px){
            font-size: 1rem;
        }
        &::before{
            content: "";
            position: absolute;
            bottom: 0;
            width: 7.4rem;
            height: .33rem;
            background-color: var(--background-light-color-2);
            border-radius: 15px;
            left: 0;
        }

        &::after{
            content: "";
            position: absolute;
            bottom: 0;
            width: 3.5rem;
            height: .33rem;
            background-color: var(--primary-color);
            border-radius: 15px;
            left: 0;
        }
        span{
            font-weight: 400;
            color: rgba(25, 29, 43, .44);
            font-size: 5rem;
            position: absolute;
            left: 0;
            top: 5%;
            z-index: -1;
            @media screen and (max-width: 620px){
                font-size: 2.5rem;
            }

            @media screen and (max-width: 496px){
                font-size: 1.8rem;
            }

            @media screen and (max-width: 370px){
                font-size: 1rem;
        
        } 
        } 
    }

`;

export default Title;
